.character_box{
    background:rgba(0,0,0,0.2);
    width:150px;
    height:150px;
    margin:10px;
    border:5px solid white;
    border-radius:100%;
    box-shadow:1px 1px 5px rgba(0,0,0,0.3);
    box-sizing:border-box;
    overflow:hidden;
    background-size:cover; 
    background-position:center;
    display:flex;
    align-items:flex-end;
    cursor: pointer;  
}

.character_box:hover{
     box-shadow:0px 10px 5px rgba(0,0,0,0.4) ;
    -webkit-transform:translateY(-5px);
            transform:translateY(-5px);
    -webkit-trasform:translateY(-5px);
    -moz-trasform:translateY(-5px);
    -ms-trasform:translateY(-5px);
    -o-trasform:translateY(-5px);
    transition:all 0.2s linear;
    -webkit-transition:all 0.2s linear;
    -moz-transition:all 0.2s linear;
    -ms-transition:all 0.2s linear;
    -o-transition:all 0.2s linear;
    
}



.character_box .character_name{
  background:transparent;
    height:100%;
    width:100%;
    border-radius:100%;
    color:transparent;
    font-weight:100;
    box-sizing: border-box;
    font-size:18px;
    display:flex;
    justify-content:center;
    align-items:center;
    padding:5px 50%;
}


.character_name:hover{
    color:white;
    height: 100%;
    background: rgba(0,0,0,0.4); 
    transition:all 0.2s linear;
    -webkit-transition:all 0.2s linear;
    -moz-transition:all 0.2s linear;
    -ms-transition:all 0.2s linear;
    -o-transition:all 0.2s linear;
  }


.loader{
    background: white;
    display: flex;
    justify-content: center;
    align-items:center;
    box-sizing: border-box;
    padding:10px 0;
    height:100vh;
}

.img_load{
    height: 180px;
}
.banner{
    background:gray;
    height:650px; 
    width:100%;
    background-size:cover;
    background-position: top;
    overflow:hidden;

}

.banner_img{
    width:100%;
    height:100%;
    -webkit-filter:brightness(0.6);
            filter:brightness(0.6);
    -webkit-transform:scale(1.2);
            transform:scale(1.2);
    -webkit-trasform:scale(1.05);
    -moz-trasform:scale(1.05);
    -ms-trasform:scale(1.05);
    -o-trasform:scale(1.05);
    transition:all 0.5s linear;
    -webkit-transition:all 0.5s linear;
    -moz-transition:all 0.5s linear;
    -ms-transition:all 0.5s linear;
    -o-transition:all 0.5s linear;
}

.banner_img:hover{
    -webkit-filter:brightness(0.8);
            filter:brightness(0.8);
    -webkit-transform:scale(1.01);
            transform:scale(1.01);
    -webkit-trasform:scale(1.05);
    -moz-trasform:scale(1.05);
    -ms-trasform:scale(1.05);
    -o-trasform:scale(1.05);
    transition:all 0.5s linear;
    -webkit-transition:all 0.5s linear;
    -moz-transition:all 0.5s linear;
    -ms-transition:all 0.5s linear;
    -o-transition:all 0.5s linear;
}
